.remediesContainer {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.remediesHeaderContainer {
    padding-top: var(--headerHeight);
    background-color: #BC414B;
    height: 8vh;
    color: #FFF;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
}

.remediesHeader {
    font-weight: 700;
    font-size: 20px;
}

.remediesBodyText {
    width: 50%;
}

.remediesHeaderText{
    width: 70%;
    margin-left: 20px;
    text-align: justify;
}

.partName{
    position: absolute;

    align-self: flex-start;
    /* right: 20px; */
}

.remediesBodyContainer {
    height: 78vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 5px;
}

.remediesBodyHeader {
    color: #249FA6;
    font-weight: 900;
    font-size: 30px;
}

.remediesContent{
    width: 95%;
    height: 100%;
    display: flex;
}
/* .bodySelect, .youtubeContainer{
    flex-basis: 50%;
} */
.bodySelect{
    flex-basis: 40%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.youtubeContainer{
    flex-basis: 60%;
    display: flex;
    flex-direction: column;
    /* align-items: center; */
    justify-content: center;
}

.remarksContainer{
    width: 100%;
    height: 50px;
    text-align: left;
    font-weight: 800;
    font-size: 1.2rem;
    padding-left: 20px;
    display: flex;
    align-items: center;
}

.youtube{
    padding-left: 20px;
    display: flex;
}

.yesBtn, .noBtn{
    margin-left: 20px;
    width: 60px;
    height: 30px;
}
.yesBtn{
    background-color: transparent;
    border: 2px solid #00AA66;
}
.yesBtn:hover{
    color: white;
    background-color: rgba(0, 170, 102, 0.5);
    border: 2px solid rgba(0, 170, 102, 0.5) !important;
}
.noBtn{
    background-color: transparent;
    border: 2px solid #BC414B;
}
.noBtn:hover{
    color: white;
    background-color: rgba(188, 65, 75, 0.5);
    border: 2px solid rgba(188, 65, 75, 0.5) !important;
}

g #neck:hover{
    fill:blue;
}